const _hoisted_1 = /*#__PURE__*/_createStaticVNode("<div class=\"product-Gateway\" data-v-154af7de><p class=\"title\" data-v-154af7de>INFINI Gateway 的关键功能</p><div class=\"content1\" data-v-154af7de><div class=\"content1-left\" data-v-154af7de><img src=\"" + _imports_0 + "\" data-v-154af7de></div><div class=\"content1-right\" data-v-154af7de><dl data-v-154af7de><dt data-v-154af7de><h3 data-v-154af7de>权限管控与安全审计</h3></dt><dd data-v-154af7de>• LDAP/AD 无缝集成</dd><dd data-v-154af7de>• TLS/HTTPS 一键开启</dd><dd data-v-154af7de>• 支持多集群的统一权限控制</dd><dd data-v-154af7de>• 支持灵活通用的请求控制</dd><dd data-v-154af7de>• 防暴力身份验证破解</dd><dd data-v-154af7de>• 黑白名单过滤控制</dd><dd data-v-154af7de>• 请求频率及宽带限制</dd><dd data-v-154af7de>• 高并发支持、限速、限流</dd></dl></div></div><div class=\"content2\" data-v-154af7de><div class=\"content2-left\" data-v-154af7de><h3 data-v-154af7de>备份及异地容灾</h3><br data-v-154af7de><div class=\"advantages\" data-v-154af7de><dl data-v-154af7de><dt data-v-154af7de><p data-v-154af7de> 对于关键的业务场景，可以使用 INFINI Gateway 来构建数据中心级别的异地容灾解决方案 </p></dt><dd data-v-154af7de>• 无缝透明，应用无需任何调整</dd><dd data-v-154af7de>• 业务操作级别的复制，跨版本兼容</dd><dd data-v-154af7de>• 支持一对多写</dd><dd data-v-154af7de>• 双集群高可用，随时切换</dd><dd data-v-154af7de>• 后端读写故障对前端业务无感知</dd><dd data-v-154af7de>• 节点故障自动处理，请求不丢失</dd><dd data-v-154af7de>• 支持本地磁盘队列和 Kafka</dd><dd data-v-154af7de>• 结合快照和 Translog 可以重做索引</dd><dd data-v-154af7de>• 通过校验任务确保三方数据完全一致</dd><dd data-v-154af7de>• 自带四层网络虚拟IP高可用</dd></dl></div></div><div class=\"content2-right\" data-v-154af7de><img src=\"" + _imports_1 + "\" data-v-154af7de></div></div><div class=\"content3\" data-v-154af7de><div class=\"content3-left\" data-v-154af7de><img src=\"" + _imports_2 + "\" data-v-154af7de></div><div class=\"content3-right\" data-v-154af7de><dl data-v-154af7de><dt data-v-154af7de><h3 data-v-154af7de>索引重建</h3><p data-v-154af7de> 修改 Mapping、修改分词词典都需要重建索引。借助 INFINI Gateway,可以做到<br data-v-154af7de>一键索引重建，对于前端应用完全无感知，确保完成后数据的一致性。 </p></dt></dl></div></div><div class=\"content4\" data-v-154af7de><div class=\"content4-left\" data-v-154af7de><dl data-v-154af7de><dt data-v-154af7de><h3 data-v-154af7de>限流限速</h3><p data-v-154af7de> 突发流量会把 Elasticsearch 集群打爆，个别大索引造成整个集群的性能窘迫。INFINI Gateway 保护击造成不良影响。 </p></dt></dl></div><div class=\"content4-right\" data-v-154af7de><img src=\"" + _imports_3 + "\" data-v-154af7de></div></div><div class=\"content3\" style=\"margin-top:150px;\" data-v-154af7de><div class=\"content3-left\" data-v-154af7de><img src=\"" + _imports_4 + "\" data-v-154af7de></div><div class=\"content3-right\" data-v-154af7de><dl data-v-154af7de><dt data-v-154af7de><h3 data-v-154af7de>查询加速</h3><p data-v-154af7de> INFINI Gateway 内置缓存功能，能够将最常见的查询进行缓存，还可指定周期性的查询计划来预热特定的查询，保证前端的业务每次都能命中查询，从而提升查询速度。 </p></dt></dl></div></div><div class=\"content4\" data-v-154af7de><div class=\"content4-left\" data-v-154af7de><dl data-v-154af7de><dt data-v-154af7de><h3 data-v-154af7de>索引加速</h3><p data-v-154af7de> 通过精准的分片级别的路由，将索引请求合并封装直接投递到指定分片的指定节点上，避免后端 Elasticsearch 再次进行请求转发从而提升整体集群的吞吐和性能。 </p></dt></dl></div><div class=\"content4-right\" data-v-154af7de><img src=\"" + _imports_5 + "\" data-v-154af7de></div></div><div class=\"content3\" style=\"margin-top:150px;\" data-v-154af7de><div class=\"content3-left\" data-v-154af7de><img src=\"" + _imports_6 + "\" data-v-154af7de></div><div class=\"content3-right\" data-v-154af7de><dl data-v-154af7de><dt data-v-154af7de><h3 data-v-154af7de>请求分析</h3><p data-v-154af7de> 总有用户抱怨 Elasticsearch 很慢，到底哪些索引慢呢？又是哪些查询造成的？哪些用户造成的呢？INFINI Gateway 帮您全程跟踪，让您对集群内的那点事情一清二楚。 </p></dt></dl></div></div><div class=\"content4\" data-v-154af7de><div class=\"content4-left\" data-v-154af7de><dl data-v-154af7de><dt data-v-154af7de><h3 data-v-154af7de>请求干预</h3><p data-v-154af7de> 代码上线之后才发现查询语句写错了？有了 INFINI Gateway，可以在线对指定业务的指定查询进行改写，将查询语句动态修复，无需重新发布应用，方便灵活。 </p></dt></dl></div><div class=\"content4-right\" data-v-154af7de><img src=\"" + _imports_7 + "\" data-v-154af7de></div></div></div><div class=\"tutorials\" data-v-154af7de><div class=\"title\" data-v-154af7de><h2 data-v-154af7de>动手教程</h2></div><div class=\"tutorials-wrapper\" data-v-154af7de><a class=\"tutorials-item\" target=\"_blank\" href=\"https://gateway.infinilabs.com/zh/docs/tutorial/log4j2_filtering/\" data-v-154af7de><img src=\"" + _imports_8 + "\" data-v-154af7de><span data-v-154af7de>Apache Log4j 漏洞处置</span><p data-v-154af7de> Apache Log4j 是一款非常流行的开源的用于 Java 运行环境的日志记录工具包，大量的 Java 框架包括 Elasticsearch 的最新版本都使用了该组件，故影响范围非常之大。 </p></a><a class=\"tutorials-item\" target=\"_blank\" href=\"https://gateway.infinilabs.com/zh/docs/tutorial/request-logging/\" data-v-154af7de><img src=\"" + _imports_9 + "\" data-v-154af7de><span data-v-154af7de>查询请求流量日志分析</span><p data-v-154af7de> 极限网关能够跟踪记录经过网关的所有请求，可用来分析发送给 Elasticsearch 的请求情况，用于分析请求性能和了解业务运行情况。 </p></a><a class=\"tutorials-item\" target=\"_blank\" href=\"https://gateway.infinilabs.com/zh/docs/tutorial/routing_to_cluser_by_index/\" data-v-154af7de><img src=\"" + _imports_10 + "\" data-v-154af7de><span data-v-154af7de>在 Kibana 里统一访问来自不同集群的索引 </span><p data-v-154af7de> 现在有这么一个需求，客户根据需要将数据按照业务维度划分，将索引分别存放在了不同的三个集群，将一个大集群拆分成多个小集群有很多好处，比如降低了耦合… </p></a></div><div class=\"bottom-title\" data-v-154af7de><a class=\"more\" href=\"https://gateway.infinilabs.com/zh/\" data-v-154af7de>了解更多</a></div></div>", 2);

